import * as React from "react";
import {Router} from "@reach/router";
import {isBrowser} from "../helpers/helpers";
import Layout from "../components/Layout/Layout";
import PrivateRoute from "../components/PrivateRoute/PrivateRoute";
import Addition from "../components/Addition/Addition";

const AdditionPage = () => {
  console.log('process.env.GATSBY_PUBLIC_URL', process.env.GATSBY_PUBLIC_URL)
  console.log(process.env.NODE_ENV)
  const basepath = process.env.NODE_ENV === "development" ? "/" : "/sumApp"
  return (
    <Layout>
      {/* {isBrowser
        ? */}
          <Router basepath={basepath}>
              <PrivateRoute path="/addition" component={Addition} />
          </Router>
        {/* // : <></>
      // } */}
    </Layout>
  )
}

export default AdditionPage;